import { Fonts, getUser, styled } from "@true/core";
import React, { memo, useCallback } from "react";
import { FlexCenter, IBase64, STextDivider } from "@/utils/helper";
import { useAnnouncements } from "@/store/announcements";
import { Buffer } from "buffer";
import { getChannels } from "@/store/channels";
import { UIText } from "@true/ui";
import { navigateToDetailPostScreen } from "@/utils";
import { Pressable } from "react-native";
import { IC_FILLED_PERSON, IC_FILLED_PUBLIC, IC_FILLED_SCHEDULE } from "@true/asset";
import moment from "moment";
import { globalStyles } from "@/utils/globalStyle";

interface AnnouncementThumbnailProps {
  id: number | undefined;
}

export const AnnouncementThumbnail = memo(function(
  props: AnnouncementThumbnailProps
) {
  const { id } = props;
  const data = useAnnouncements(id);
  const channel = getChannels(data?.channel_id || 0);
  const coverImage: IBase64 = JSON.parse(
    Buffer.from(data?.image || "", "base64").toString("utf-8")
  );
  const onNavigateToDetail = useCallback(() => {
    navigateToDetailPostScreen({
      id: id || 0,
      user_id: data?.user_id || 0,
      time: data?.since || 0,
      hid: data?.hid || ""
    });
  }, [id]);

  return (
    <SContainer onPress={onNavigateToDetail}>
      <SImageView>
        <SImage source={{ uri: coverImage.image }} resizeMode={"cover"} />
      </SImageView>
      <SInfoView>
        <UIText.HeaderSmall numberOfLines={2}>{data?.name}</UIText.HeaderSmall>
        <SRowCenter style={globalStyles.mt4}>
          <SIcon source={IC_FILLED_PUBLIC} />
          <STextGrey numberOfLines={1}>{channel?.name}</STextGrey>
        </SRowCenter>
        <SRowCenter>
          <SIcon source={IC_FILLED_PERSON} />
          <STextGrey numberOfLines={1}>{channel?.name}</STextGrey>
        </SRowCenter>
        <SRowCenter>
          <SIcon source={IC_FILLED_SCHEDULE} />
          <STextGrey numberOfLines={1}>{moment(
            // @ts-ignore*/}
            data?.since * 1000
          ).format("HH:mm DD/MM/YYYY")}</STextGrey>
          {data?.seens && data?.seens?.length > 0 ?
            <>
              <STextDivider> | </STextDivider>
              <STextGreen>{`Seen by ${data?.seens?.length}`} </STextGreen>
            </> : null}
        </SRowCenter>
      </SInfoView>
    </SContainer>
  );
});
const SContainer = styled(Pressable)`
  flex-direction: row;
  padding: 16px;
  background-color: white;
  border-bottom-width: 1px;
  border-bottom-color: ${p => p.theme.neutral6};
`;
const SInfoView = styled.View`
  flex: 3;
`;
const SImageView = styled.View`
  display: flex;
  flex: 2;
  margin-right: 12px;
  background-color: ${p => p.theme.neutral7};
  border-radius: 8px;
`;
const SImage = styled.Image`
  height: 100%;
  width: 100%;
  max-height: 100px;
  border-radius: 8px;
`;
const STextGrey = styled(UIText.CaptionMedium400)`
  color: ${p => p.theme.neutral2};
`;
const STextGreen = styled(STextGrey)`
  color: #05944f;
`;
const SRowCenter = styled(FlexCenter)`
  margin-bottom: 8px;
`;
const SIcon = styled.Image`
  width: 16px;
  height: 16px;
  tint-color: ${p => p.theme.neutral2};
  margin-right: 4px;
`;
