import React, { memo, useCallback } from "react";
import { CustomHeader } from "@/components/CustomHeader";
import { navigateToCreatePostScreen } from "@/utils";
import { ScreenWrapper, ScrollableTabView } from "@true/ui";
import { AnnouncementThumbnail } from "@/screens/AnnouncementScreen/AnnouncementThumbnail";
import { requestAllAnnouncements, requestCreatedAnnouncements } from "@/store/announcements/function";
import { getAnnouncementsByQuery, useAnnouncementsByQuery } from "@/store/announcements";
import { FilterAnnouncement } from "@/screens/AnnouncementScreen/FilterAnnouncement";

export const AnnouncementScreen = memo(function() {
  const renderItem = useCallback(
    (item: any, index?: number, isLast?: boolean) => {
      return <AnnouncementThumbnail id={item} key={index} />;
    },
    []
  );
  const onCreate = useCallback(() => {
    navigateToCreatePostScreen({
      type: "announcement"
    });
  }, []);
  return (
    <ScreenWrapper isGrey={true}>
      <CustomHeader
        title={"Announcements"}
        isSearch={false}
        isBack
        onCreate={onCreate}
      />
      <ScrollableTabView.Provider
        params={{}}
        renderItem={renderItem}
        getItemByQuery={getAnnouncementsByQuery}
        useItemByQuery={useAnnouncementsByQuery}
        tabs={[
          {
            title: "All",
            content: p => (
              <ScrollableTabView.List
                index={p.index}
                ListHeaderComponent={<FilterAnnouncement />}
                requestFn={requestAllAnnouncements}
              />
            )
          },
          {
            title: "Created by me",
            content: p => (
              <ScrollableTabView.List
                index={p.index}
                ListHeaderComponent={<FilterAnnouncement />}
                requestFn={requestCreatedAnnouncements}
                query={"created"}
              />
            )
          }
        ]}>
        <ScrollableTabView.TabBar />
        <ScrollableTabView.Content />
      </ScrollableTabView.Provider>
    </ScreenWrapper>
  );
});
