import {Core, Fetch} from '@true/core';
import {batch} from 'react-redux';
import {IAnnouncement} from '@/store/announcements/type';
import {
  setAnnouncementsQuery,
  syncAnnouncements,
} from '@/store/announcements/index';

export const requestAllAnnouncements = async () => {
  const {data} = await Fetch.postWithToken<{
    anns: IAnnouncement[];
  }>(`@${Core.systemPath}/inside/ajax/api/announcement/gets`);

  batch(() => {
    if (data.anns.length) {
      syncAnnouncements(data.anns);
      setAnnouncementsQuery(prev => ({
        ...prev,
        ['all']: data.anns.map(item => item.id.toString()),
      }));
    }
  });
  return data.anns;
};

export const requestCreatedAnnouncements = async () => {
  const {data} = await Fetch.postWithToken<{
    anns: IAnnouncement[];
  }>(`@${Core.systemPath}/inside/ajax/api/announcement/gets?created=mine`);
  batch(() => {
    if (data.anns.length) {
      syncAnnouncements(data.anns);
      setAnnouncementsQuery(prev => ({
        ...prev,
        ['created']: data.anns.map(item => item.id.toString()),
      }));
    }
  });
  return data.anns;
};

interface IRequestAnnouncementsChannel {
  path: string;
  q?: string;
}

export const requestAnnouncementsChannel = async (
  params: IRequestAnnouncementsChannel,
) => {
  console.log('params', params);
  const {data} = await Fetch.postWithToken<{
    announcements: IAnnouncement[];
  }>(
    `@${Core.systemPath}/inside/ajax/api/channel/update/announcement?path=${
      params.path
    }${params?.q ? '&q=' + params.q : ''}`,
  );
  batch(() => {
    if (data.announcements.length) {
      syncAnnouncements(data.announcements);
      setAnnouncementsQuery(prev => ({
        ...prev,
        [params.path]: data.announcements.map(item => item.id.toString()),
      }));
    }
  });
  return data.announcements;
};

export const requestCreatedAnnouncementsChannel = async (
  params: IRequestAnnouncementsChannel,
) => {
  const {data} = await Fetch.postWithToken<{
    announcements: IAnnouncement[];
  }>(
    `@${
      Core.systemPath
    }/inside/ajax/api/channel/update/announcement?created=mine&path=${
      params.path
    }${params?.q ? '&q=' + params.q : ''}`,
  );
  batch(() => {
    if (data.announcements.length) {
      // syncAnnouncements(data.announcements);
      setAnnouncementsQuery(prev => ({
        ...prev,
        [`created_${params.path}`]: data.announcements.map(item =>
          item.id.toString(),
        ),
      }));
    }
  });
  return data.announcements;
};
